import TadpoleTail from './TadpoleTail';
export default class Tadpole {
  constructor() {
    this.x = Math.random() * 300 - 150; // x
    this.y = Math.random() * 300 - 150; // y
    this.size = 4; // 

    this.name = ''; // 
    this.age = 0; // 
    this.sex = -1; //  -101
    this.icon = 'img/default.jpg'; // 
    this.img = {}; // img
    this.hover = false;

    this.momentum = 0; // 
    this.maxMomentum = 3; // 
    this.angle = Math.PI * 2; // 

    this.targetX = 0; // x
    this.targetY = 0; // y
    this.targetMomentum = 0; // 

    this.messages = []; // 
    this.timeSinceLastActivity = 0; // 

    this.changed = 0;
    this.timeSinceLastServerUpdate = 0; // 

    this.tail = new TadpoleTail(this);
  }

  /**
   * 
   * @params mouse 
   * **/
  update(mouse) {
    this.timeSinceLastServerUpdate++;

    // 
    this.x += Math.cos(this.angle) * this.momentum;
    this.y += Math.sin(this.angle) * this.momentum;

    if (this.targetX != 0 || this.targetY != 0) {
      this.x += (this.targetX - this.x) / 20;
      this.y += (this.targetY - this.y) / 20;
    }

    // 
    for (let i = this.messages.length - 1; i >= 0; i--) {
      const msg = this.messages[i];
      msg.update();

      if (msg.age == msg.maxAge) {
        this.messages.splice(i, 1);
      }
    }

    // Update tadpole hover/mouse state
    if (Math.sqrt(Math.pow(this.x - mouse.worldx, 2) + Math.pow(this.y - mouse.worldy, 2)) < this.size + 6) {
      this.hover = true;
      mouse.tadpole = this;
    } else {
      if (mouse.tadpole && mouse.tadpole.id == this.id) {
        //mouse.tadpole = null;
      }
      this.hover = false;
    }

    // 
    this.tail.update();
  }

  //  
  onclick(e) {
    console.log('eeee:', e);
    // 
    if (e.which == 1) {
      if (this.hover) {
        return true;
      }
      //
    } else if (e.which == 2) {
      //todo:open menu
      e.preventDefault();
      return true;
    }
    return false;
  }

  /**
   * 
   * @params tadpoles 
   * @params angleTargetX Xcanvas
   * @params angleTargetY Ycanvas
   * **/
  userUpdate(tadpoles, angleTargetX, angleTargetY) {
    this.age++;

    const prevState = {
      angle: this.angle, // 
      momentum: this.momentum, // 
    };

    // xy
    let anglediff = Math.atan2(angleTargetY - this.y, angleTargetX - this.x) - this.angle;
    while (anglediff < -Math.PI) {
      anglediff += Math.PI * 2;
    }
    while (anglediff > Math.PI) {
      anglediff -= Math.PI * 2;
    }

    this.angle += anglediff / 5;

    // Momentum to targetmomentum
    if (this.targetMomentum != this.momentum) {
      this.momentum += (this.targetMomentum - this.momentum) / 20;
    }

    if (this.momentum < 0) {
      this.momentum = 0;
    }

    this.changed += Math.abs((prevState.angle - this.angle) * 3) + this.momentum;

    if (this.changed > 1) {
      this.timeSinceLastServerUpdate = 0;
    }
  }

  draw(context) {
    const opacity = Math.max(Math.min(20 / Math.max(this.timeSinceLastServerUpdate - 300, 1), 1), 0.2).toFixed(3);

    // if (this.hover) {
    //   this.drawIcon(context);
    // }
    // this.drawIcon(context); // 
    // 
    if (this.sex == 0) {
      context.fillStyle = 'rgba(255, 100, 140,' + opacity + ')';
    } else if (this.sex == 1) {
      context.fillStyle = 'rgba(100, 140, 247,' + opacity + ')';
    } else {
      context.fillStyle = 'rgba(226,219,226,' + opacity + ')';
    }

    context.shadowOffsetX = 0;
    context.shadowOffsetY = 0;
    context.shadowBlur = 6;
    context.shadowColor = 'rgba(255, 255, 255, ' + opacity * 0.7 + ')';

    // Draw circle
    context.beginPath();
    context.arc(this.x, this.y, this.size, this.angle + Math.PI * 2.7, this.angle + Math.PI * 1.3, true);

    this.tail.draw(context);

    context.closePath();
    context.fill();

    context.shadowBlur = 0;
    context.shadowColor = '';

    this.drawName(context);
    this.drawMessages(context);
  }

  drawName(context) {
    const opacity = Math.max(Math.min(20 / Math.max(this.timeSinceLastServerUpdate - 300, 1), 1), 0.2).toFixed(3);
    context.fillStyle = 'rgba(226,219,226,' + opacity + ')';
    context.font = 7 + "px 'proxima-nova-1','proxima-nova-2', arial, sans-serif";
    context.textBaseline = 'hanging';
    const width = context.measureText(this.name).width;
    context.fillText(this.name, this.x - width / 2, this.y + 8);
  }

  drawMessages(context) {
    this.messages.reverse();
    for (let i = 0, len = this.messages.length; i < len; i++) {
      this.messages[i].draw(context, this.x + 10, this.y + 5, i);
    }
    this.messages.reverse();
  }

  // 
  drawIcon(context) {
    // img
    if (!this.img || !this.img.src || (this.icon && this.img.src != this.icon)) {
      let img = new Image();
      img.src = this.icon;
      img.onerror = function() {
        img.src = 'img/default.jpg';
      };
      this.img = img;
    }

    if (this.img.complete) {
      let w = this.img.width;
      let h = this.img.height;
      w = w / h >= 1 ? 20 : (20 * w) / h;
      h = h / w >= 1 ? 20 : (20 * h) / w;
      let x = this.x - 10;
      let y = this.y - 28;
      context.drawImage(this.img, x, y, w, h);
      context.fillStyle = 'rgba(0,0,0,0)';
      context.strokeStyle = '#fff';
      context.linewidth = 10;
      context.fillRect(x, y, w, h);
      context.strokeRect(x, y, w, h);
      context.closePath();
    }
  }
}
